.title {
   font-family: $secondary-font;
   font-size: 34px;
   line-height: 1.14;
   letter-spacing: 0.06em;
   text-transform: uppercase;
   color: $primary-accent-color;
   @media screen and (min-width: 768px) {
      font-size: 48px;
   }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
  }

}
